import React from "react";
import { Select } from "antd";
import { useQuery } from "react-query3";
import { Environment } from "../../types";

export default function EnvironmentSelectBox(props) {
  const { data, isLoading } = useQuery<Environment[]>("/environment");
  return (
    <Select {...props} loading={isLoading}>
      <Select.Option key={`default`} value={null}>Default</Select.Option>
      {data?.map((env) => (
        <Select.Option key={env.name} value={env.name}>{env.name}</Select.Option>
      ))}
    </Select>
  );
}
